import * as React from 'react';
import { RouteComponentProps } from 'react-router-dom';

import { LoginFormConnected as LoginForm } from '../connected';
import FormSpinner from '@src/components/formSpinner';
import {
  FormPageLayout,
  FormPage,
  FormPageLogotype,
  FormPageLogotypeHeader,
  FormSmallHeader,
  FormAnchor,
  FormAnchorSpan
} from '@src/core/styled';

interface LoginProps extends RouteComponentProps<void> {
  loginView: string,
}

export const Login: React.SFC<LoginProps> = (props) => {
  const { loginView } = props;
  
  switch (loginView) {
    case 'pending': return (
      <FormPageLayout>
        <FormPage>
          <FormPageLogotype>
            <FormPageLogotypeHeader>
              {' '}
            </FormPageLogotypeHeader>
          </FormPageLogotype>
          <FormSpinner />
        </FormPage>
      </FormPageLayout>
    );
    default: return (
      <FormPageLayout>
        <FormPage>
          <FormPageLogotype>
            <FormPageLogotypeHeader>
              {' '}
            </FormPageLogotypeHeader>
          </FormPageLogotype>
          <LoginForm />
          <FormSmallHeader color={'grey'}>{'  ?'}</FormSmallHeader>
          <FormAnchor to={'/registration'}>
            <FormAnchorSpan>{''}</FormAnchorSpan>
          </FormAnchor>
          <FormSmallHeader color={'grey'}>{' ?'}</FormSmallHeader>
          <FormAnchor to={'/recovery'}>
            <FormAnchorSpan>{' '}</FormAnchorSpan>
          </FormAnchor>
        </FormPage>
      </FormPageLayout>
    );
  };
}
